/**
 * Loader
 *
 * Page Loader component to be used with Loadable
 */

import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import styled from 'styled-components';

import BaseLoader from './BarLoader';

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 400px;
`;

const Loader = props => {
  const { className, style, ...otherProps } = props;
  return (
    <Wrapper className={cx('___g-loader', className)} style={style}>
      <BaseLoader className="___g-loader__bar" width={160} {...otherProps} />
    </Wrapper>
  );
};

Loader.propTypes = {
  /** Add additional class name to wrapper element */
  className: PropTypes.string,

  /** Override wrapper styling using inline style */
  style: PropTypes.object,
};

export const BarLoader = BaseLoader;
export default Loader;
